	module.exports = "<article class=\"markdown-body\">\n    <h1 id=\"mdradio\"><a class=\"header-link\" href=\"#mdradio\"></a>MdRadio</h1>\n\n    <p>Radio buttons allow the user to select one option from a set. Use radio\n        buttons for exclusive selection if you think that the user needs to see\n        all available options side-by-side.</p>\n    <p class=\"img-container\">\n        <img src=\"https://material.angularjs.org/material2_assets/radio/radios.png\"\n        alt=\"Preview\">\n    </p>\n    \n<h3 id=\"examples\"><a class=\"header-link\" href=\"#examples\"></a>Examples</h3>\n\n    <p>A basic radio group would have the following markup.</p>\n<pre class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-group</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-button</span> <span class=\"hljs-attr\">value</span>=<span class=\"hljs-string\">\"option_1\"</span>&gt;</span>1<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-button</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-button</span> <span class=\"hljs-attr\">value</span>=<span class=\"hljs-string\">\"option_2\"</span>&gt;</span>2<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-button</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-group</span>&gt;</span></code></pre>\n    <p>A dynamic example, populated from a <code>data</code> variable:</p>\n    <pre\n    class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-group</span> [(<span class=\"hljs-attr\">value</span>)]=<span class=\"hljs-string\">\"groupValue\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-button</span> *<span class=\"hljs-attr\">ngFor</span>=<span class=\"hljs-string\">\"let d of data\"</span> [<span class=\"hljs-attr\">value</span>]=<span class=\"hljs-string\">\"d.value\"</span>&gt;</span>\n    {{d.label} } \n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-button</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-group</span>&gt;</span></code>\n        </pre>\n        <p>A dynamic example for use inside a form showing support for <code>[(ngModel)]</code>:</p>\n        <pre\n        class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-group</span> [(<span class=\"hljs-attr\">ngModel</span>)]=<span class=\"hljs-string\">\"chosenOption\"</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">md-radio-button</span> *<span class=\"hljs-attr\">ngFor</span>=<span class=\"hljs-string\">\"let o of options\"</span> [<span class=\"hljs-attr\">value</span>]=<span class=\"hljs-string\">\"o.value\"</span>&gt;</span>\n    {{o.label} } \n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-button</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">md-radio-group</span>&gt;</span></code>\n            </pre>\n            <h2 id=\"-md-radio-group-\"><a class=\"header-link\" href=\"#-md-radio-group-\"></a><code>&lt;md-radio-group&gt;</code></h2>\n\n            \n<h3 id=\"properties\"><a class=\"header-link\" href=\"#properties\"></a>Properties</h3>\n\n            <table>\n                <thead>\n                    <tr>\n                        <th>Name</th>\n                        <th>Type</th>\n                        <th>Description</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td><code>selected</code>\n                        </td>\n                        <td><code>MdRadioButton</code>\n                        </td>\n                        <td>The currently selected button.</td>\n                    </tr>\n                    <tr>\n                        <td><code>value</code>\n                        </td>\n                        <td><code>any</code>\n                        </td>\n                        <td>The current value for this group.</td>\n                    </tr>\n                    <tr>\n                        <td><code>disabled</code>\n                        </td>\n                        <td><code>boolean</code>\n                        </td>\n                        <td>Whether the group is disabled.</td>\n                    </tr>\n                </tbody>\n            </table>\n            <p>When selection is changed, an event is emitted from the <code>change</code> EventEmitter\n                property.</p>\n            \n<h3 id=\"notes\"><a class=\"header-link\" href=\"#notes\"></a>Notes</h3>\n\n            <p>The <code>md-radio-group</code> component has no button initially selected.</p>\n            <h2\n            id=\"-md-radio-button-\"><a class=\"header-link\" href=\"#-md-radio-button-\"></a><code>&lt;md-radio-button&gt;</code>\n                </h2>\n                \n<h3 id=\"properties-1\"><a class=\"header-link\" href=\"#properties-1\"></a>Properties</h3>\n\n                <table>\n                    <thead>\n                        <tr>\n                            <th>Name (attribute)</th>\n                            <th>Type</th>\n                            <th>Description</th>\n                        </tr>\n                    </thead>\n                    <tbody>\n                        <tr>\n                            <td><code>id</code>\n                            </td>\n                            <td><code>string</code>\n                            </td>\n                            <td>The unique ID of this radio button.</td>\n                        </tr>\n                        <tr>\n                            <td><code>name</code>\n                            </td>\n                            <td><code>string</code>\n                            </td>\n                            <td>Group name, defaults to parent radio group if present.</td>\n                        </tr>\n                        <tr>\n                            <td><code>value</code>\n                            </td>\n                            <td><code>any</code>\n                            </td>\n                            <td>The value of this radio button.</td>\n                        </tr>\n                        <tr>\n                            <td><code>checked</code>\n                            </td>\n                            <td><code>boolean</code>\n                            </td>\n                            <td>Whether the radio is checked.</td>\n                        </tr>\n                        <tr>\n                            <td><code>disabled</code>\n                            </td>\n                            <td><code>boolean</code>\n                            </td>\n                            <td>Whether the radio is disabled.</td>\n                        </tr>\n                        <tr>\n                            <td><code>aria-label</code>\n                            </td>\n                            <td><code>string</code>\n                            </td>\n                            <td>Used to set the <code>aria-label</code> attribute of the underlying input\n                                element.</td>\n                        </tr>\n                        <tr>\n                            <td><code>aria-labelledby</code>\n                            </td>\n                            <td><code>string</code>\n                            </td>\n                            <td>Used to set the <code>aria-labelledby</code> attribute of the underlying\n                                input element.</td>\n                        </tr>\n                    </tbody>\n                </table>\n<pre class=\"hljs\"><code>                             <span class=\"hljs-keyword\">If</span> provided, this attribute takes precedence <span class=\"hljs-keyword\">as</span> the element'<span class=\"hljs-keyword\">s</span> text alternative. |</code></pre>\n                <p>When checked, an event is emitted from the <code>change</code> EventEmitter\n                    property.</p>\n                \n<h3 id=\"notes-1\"><a class=\"header-link\" href=\"#notes-1\"></a>Notes</h3>\n\n                <ul class=\"list\">\n                    <li>The <code>md-radio-button</code> component by default uses the accent color\n                        from the theme palette.</li>\n                </ul>\n</article>"

