	module.exports = "<article class=\"markdown-body\">\n    <h1 id=\"mdliveannouncer\"><a class=\"header-link\" href=\"#mdliveannouncer\"></a>MdLiveAnnouncer</h1>\n\n    <p><code>MdLiveAnnouncer</code> is a service, which announces messages to\n        several screenreaders.</p>\n    \n<h3 id=\"methods\"><a class=\"header-link\" href=\"#methods\"></a>Methods</h3>\n\n    <table>\n        <thead>\n            <tr>\n                <th>Name</th>\n                <th>Description</th>\n            </tr>\n        </thead>\n        <tbody>\n            <tr>\n                <td><code>announce(message, politeness)</code>\n                </td>\n                <td>This announces a text message to the supported screenreaders.\n                    <br>\n                    <br>The politeness parameter sets the <code>aria-live</code> attribute on the\n                    announcer element</td>\n            </tr>\n        </tbody>\n    </table>\n    \n<h3 id=\"examples\"><a class=\"header-link\" href=\"#examples\"></a>Examples</h3>\n\n    <p>The service can be injected in a component.</p>\n<pre class=\"hljs\"><code>@Component({\n  selector: <span class=\"hljs-string\">'my-component'</span>\n  providers: [MdLiveAnnouncer]\n} )\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">class</span> MyComponent {\n\n <span class=\"hljs-keyword\">constructor</span>(live: MdLiveAnnouncer) {\n   live.announce(<span class=\"hljs-string\">\"Hey Google\"</span>);\n } \n\n} </code></pre>\n    <h3 id=\"supported-screenreaders\"><a class=\"header-link\" href=\"#supported-screenreaders\"></a>Supported Screenreaders</h3>\n\n    <ul class=\"list\">\n        <li>JAWS (Windows)</li>\n        <li>NVDA (Windows)</li>\n        <li>VoiceOver (OSX and iOS)</li>\n        <li>TalkBack (Android)</li>\n    </ul>\n</article>"

